<template>
	<div class="coop">
		<div class="banner">
			<img src="../assets/cooperation/lunbo.png"/>
			<div class="container">			
				<div class="wrap">
					<div class="nizai zhihui">智慧</div>
					<p class="women">为酒店注入新活力</p>
				</div>
				
			</div>

			<div class="mengban"></div>
			
		</div>
		<div class="advantage">
			<img src="../assets/cooperation/hezuoyoushi.png"/>
		</div>
		<div class="process">
			<h4>合作流程</h4>
			<div>
				<img src="../assets/cooperation/liuchen.png"/>
			</div>
		</div>
		<div class="formTijiao">
			<h4>意向提交</h4>
			<div class="form-item">
				<div class="form-item-child item-left">
					<label>*姓名</label>
					<input type="text" v-model="name" class="xingming" @change="clearClass($event)"/>
				</div>
				<div class="form-item-child item-right">
					<label>地址</label>
					<input type="text" v-model="address" class="dizhi" @change="clearClass($event)"/>
				</div>
			</div>
			<div class="form-item">
				<div class="form-item-child item-left">
					<label>*手机</label>
					<input type="text" v-model="phone" class="shouji" @change="clearClass($event)"/>
				</div>
				<div class="form-item-child item-right">
					<label>邮箱</label>
					<input type="text" v-model="email" class="youxiang" @change="clearClass($event)"/>
				</div>
			</div>
			<div class="form-item form-items-inner">
				<div class="form-item-child liuyan">
					<label>留言</label>
					<textarea v-model="comments" class="liuyanS" @change="clearClass($event)"></textarea>

				</div>
			</div>
			<div class="form-item form-items-inner">
				<div class="button-wrap">
					<button @click="sendAjax()">提交</button>
				</div>
			</div>
			<div class="tijiao_msg" v-text='"提交"+status'></div>
		</div>
		<div class="lianxi">
			<img src="../assets/cooperation/lianxi.png"/>
			
			<div class="container">
				<div class="lianxiWrap">
					<h4>联系我们</h4>
					<ul>
						<li>电话：021-62688830</li>
						<li>邮箱：coop@outuhulian.com</li>
						<li>地址: 上海市虹口区海宁路358号4楼</li>
					</ul>				
				</div>
			
			</div>

		</div>
	</div>
</template>

<script>
	import AnimateText from 'animate-text'
	function hasClass(elem, cls) {
			cls = cls || '';
			if(cls.replace(/\s/g, '').length == 0) return false;
			return new RegExp(' ' + cls + ' ').test(' ' + elem.className + ' ');
		}
		
	function addClass(ele, cls) {
			if(!hasClass(ele, cls)) {
				ele.className = ele.className == '' ? cls : ele.className + ' ' + cls;
			}
		}
		
	function removeClass(ele, cls) {
			if(hasClass(ele, cls)) {
				var newClass = ' ' + ele.className.replace(/[\t\r\n]/g, '') + ' ';
				while(newClass.indexOf(' ' + cls + ' ') >= 0) {
					newClass = newClass.replace(' ' + cls + ' ', ' ');
				}
				ele.className = newClass.replace(/^\s+|\s+$/g, '');
			}
	}
	export default{
//		name:'cooperation',
		data(){
			return {
				msg:'商务合作',
				name:'',
				address:'',
				phone:'',
				email:'',
				comments:'',
				status:'成功'
			}
		},
		methods:{
			clearClass(e){
				removeClass(e.target,'input_xianshi')
			},
			sendAjax(){
				var name1 = document.querySelector('.xingming')
				var email1 = document.querySelector('.youxiang')
				var address1 = document.querySelector('.dizhi')
				var phone1 = document.querySelector('.shouji')
				var comments1 = document.querySelector('.liuyanS')
				var tijiaoMsg = document.querySelector('.tijiao_msg')
				removeClass(name1,'input_xianshi')
				removeClass(email1,'input_xianshi')
				removeClass(address1,'input_xianshi')
				removeClass(phone1,'input_xianshi')
				removeClass(comments1,'input_xianshi')
				if(this.name.trim()==''){
					addClass(name1,'input_xianshi')
					return
				}
				if(this.address.trim()==''){
					addClass(address1,'input_xianshi')
					return
				}
				if(this.phone.trim()==''){
					addClass(phone1,'input_xianshi')
					return
				}
				if(!(/^1[34578]\d{9}$/.test(this.phone))){
					addClass(phone1,'input_xianshi')
					return
				}
				if(this.email.trim()==''){
					addClass(email1,'input_xianshi')
					return
				}
				if(this.comments.trim()==''){
					addClass(comments1,'input_xianshi')
					return
				}
				
				if(!(/^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/.test(this.email))){
					addClass(email1,'input_xianshi')
					return
				}
				this.$http.post('http://beta.mobed.cn/app/index/webform',{
					name:this.name,
					address:this.address,
					phone:this.phone,
					email:this.email,
					comment:this.comments
				},{
					emulateJSON:true
				}).then(function(res){
						tijiaoMsg.style.display='block'
					setTimeout(function(){
							tijiaoMsg.style.display='none'
					},1000)
					if(parseInt(res.data)==1){
						this.status='成功'
					}else{
						this.status='失败'
					}
				})
			}
		},
		mounted(){
//			new AnimateText('.nizai', {
//				  time: 400, 
//				  isNumber: true,
//				  startNumber: 0, 
//				  changeCount: 32, 
//				  onAnimated: function () {
//				  	new AnimateText('.women', {
//						  time: 1000,
//						  isNumber: true, 
//						  startNumber: 0, 
//						  changeCount: 32,
//						  onAnimated: function () {
//						  	
//						  } // 动画结束事件回调
//						})
//				  } // 动画结束事件回调
//				})
		}
	}
	
//	console.log(document.documentElement.clientHeight)
</script>

<style scoped="scoped" lang="scss">
	.banner{
		height:734px;
		overflow: hidden;
		position: relative;
		img{
			float: left;
			height:100%;
		}
		.container{
			position: relative;
			height: 100%;
		}
		.wrap{
			position: absolute;
			top: 50%;
			right:124px;
			z-index: 100;
			margin-top: -55px;
			.zhihui{
				font-size:68px;
				color:#f3f3f3;
			}
			p{
				font-size:28px;
				color:#f3f3f3;
			}
			&:before{
				content: '';
				position: absolute;
				background-color:#9da1ab;
				width: 500px;
				height: 1px;
				transform-origin:45deg;
				background-color: #9ba1aa;
				transform:translateY(-50%) rotate(-60deg);
				top:20px;
				left: 50%;
				transform-origin:0 50%;
			}
			&:after{
				content: '';
				position: absolute;
				background-color:#9da1ab;
				width: 500px;
				height: 1px;
				transform-origin:45deg;
				background-color: #9ba1aa;
				transform:translateX(-20%) rotate(-60deg);
				bottom:0px;
				right:0;
				transform-origin:100% 50%;
			}
		}
		.mengban {
				position: absolute;
				width: 100%;
				height: 100%;
				background-color: rgba(10, 7, 2, 0.5);
			}
		&:after{
			display: block;
			content: '';
			clear: both;
		}
	}
	.advantage{
		img{
			float: left;
			width: 100%;
		}
		&:after{
			display: block;
			content: '';
			clear: both;
		}
	}
	.process{
		h4{
			background-color:#222223;
			font-size:28px;
			color:#eaeaea;
			height:68px;
			line-height:68px;
		}
		div{
			img{
				float: left;
				width:100%;
			}
			&:after{
				display: block;
				content:'';
				clear: both;
			}
		}
	}
	.formTijiao{
		position: relative;
		background: url(../assets/cooperation/yixiang.png) no-repeat center;
		background-size:cover;
		.tijiao_msg{
			position: absolute;
			width:148px;
			height:44px;
			line-height: 44px;
			border-radius:3px;
			background:rgba(147,146,144,0.6);
			top: 175px;
			left: 50%;
			transform: translateX(-50%);
			color:#fafafa;
			display: none;
		}
		h4{
			font-size:28px;
			color:#373738;
			/*height:96px;*/
			/*line-height:96px;*/		
			height: 45px;
			line-height: 45px;
			margin-bottom:9px;
		}
		.form-item{
			width: 50%;
			margin: 0 auto;
			padding-bottom:20px;
			.item-left{
				float: left;
				text-align: left;
			}
			.item-right{
				float: right;
				text-align:right;
			}
			.form-item-child{
				width: 50%;
				label{
					font-size:14px;
					color:#f7f7f7;
					background-color:#373738;
					width:55px;
					height: 26px;
					text-align: center;
					line-height: 26px;
					display: inline-block;
				}
				input{
					border:1px solid #373738;
					display: inline-block;
					box-sizing: border-box;
					height:26px;
					width: 65%;
					background-color: transparent;
					text-indent: 0.5em;
				}
				.input_xianshi{
					border: 1px solid #fd3f3f;
					color:#fd3f3f;
				}
			}	
			.button-wrap{
				width:-webkit-calc(100% - 60px);
				width: calc(100% - 60px);
				padding-left: 60px;
				button{
					width: 100%;
					background-color:#373738;
					color:#FFFFFF;
					font-size:28px;
					text-align: center;
					height: 44px;
					border: none;
					outline: none;
				}	
			}				
			&:after{
				display: block;
				clear: both;
				content: '';
			}
			.liuyan{
				width: 100%;
				text-align: left;
				label{
					float: left;
				}
				textarea{
					width: calc(100% - 62px);
					width: -webkit-calc(100% - 62px);
					height:144px;
					background-color: transparent;
					resize: none;
					float: right;
				}
				&:after{
					content: '';
					display: block;
					clear: both;
				}
			}
		}
		.form-items-inner{
			padding-bottom: 10px;
		}
	}
	.lianxi{
		background: rgba(10,7,2,1);
		position: relative;
		overflow: hidden;
		img{
			/*width: 100%;*/
			float: left;
			height: 481px;
		}
		.container{
			position: absolute;
			left:50%;
			top: 50%;
			transform: translate(-50%,-50%);
			.lianxiWrap{
				color:#ffffff;
				float: left;
				text-align: left;
				margin-left:50%;
				h4{
					font-size:28px;
					color:#ffffff;
					padding-bottom: 40px;
				}
				ul{
					list-style: none;
					li{
						padding-bottom: 44px;
					}
				}
			}
		}
		&:after{
			content: '';
			display: block;
			clear: both;
		}
	}
</style>


