<template>
	<div class="wrap">
		<header>
			<div class="diyiceng">
				<div class="dierceng">
					<div class="container">
						<div class="header-inner">
							<div class="site-logo">
								<router-link to="/home"><img src="../assets/cooperation/logo.png" /></router-link>
							</div>
							<nav class="primary-menu">
								<ul class="header-wrap">
									<li class="header-item">
										<router-link to='/home'>
											首页
											<span></span>
										</router-link>
									</li>
									<li class="header-item">
										<router-link to='/solution'>
											解决方案
											<span></span>
										</router-link>
									</li>
									<li class="header-item">
										<router-link to='/aboutUs'>
											关于我们
											<span></span>
										</router-link>
									</li>
									<li class="header-item">
										<router-link to='/cooperation'>
											商务合作
											<span></span>
										</router-link>
									</li>
									<li class="header-item">
										<router-link to='/joinUs'>
											加入我们
											<span></span>
										</router-link>
									</li>
								</ul>
							</nav>
							<div class="toggle" @click='changeTypes()'>
								<span v-bind:class="[togglePhone?'toggle_true':'toggle_false']"></span>
							</div>

						</div>
					</div>
					<ul :class="[togglePhone?'list_show':'list_hide','toggle_list']">
						<li class="toggle_item" @click='changeFalse'>
							<router-link to='/home'>首页</router-link>
						</li>
						<li class="toggle_item" @click='changeFalse()'>
							<router-link to='/solution'>解决方案</router-link>
						</li>
						<li class="toggle_item" @click='changeFalse()'>
							<router-link to='/aboutUs'>关于我们</router-link>
						</li>
						<li class="toggle_item" @click='changeFalse()'>
							<router-link to='/cooperation'>商务合作</router-link>
						</li>
						<li class="toggle_item" @click='changeFalse()'>
							<router-link to='/joinUs'>加入我们</router-link>
						</li>
					</ul>

				</div>

			</div>

		</header>
		<transition name='slide'>
			<router-view></router-view>
		</transition>
	</div>
</template>

<script>
	export default {
		name: 'header',
		data: function() {
			return {
				togglePhone:false
			}
		},
		methods:{
			changeTypes:function(){
				this.togglePhone = !this.togglePhone
			},
			changeFalse:function(){
				this.togglePhone=false
			}
		}
	}
</script>

<style scoped="scoped" lang="scss">
	header {
		position: fixed;
		z-index: 100000;
		width: 100%;
		top: 0;
		background-color: rgba(0, 0, 0, 0.51);
		.list_show{
			height:255px;
		}
		.list_hide{
			height: 0;
			overflow: hidden;
		}
		.toggle_list {
			list-style: none;
			width: 100%;
			transition:all .5s;
			overflow: hidden;
			li {
				border-top: 1px solid rgba(0, 0, 0, .1);
				text-align: center;
				background-color: #ff5b64;
				height: 50px;
				line-height: 50px;
				a {
					display: block;
					height: 100%;
					text-decoration: none;
					color: #fff;
				}
				&:nth-child(1) {
					border-top: none;
				}
			}
		}
		.container {
			margin: 0 auto;
			height: 98px;
			box-sizing: border-box;
			padding: 18px 0;
			.header-inner {
				.site-logo {
					float: left;
					img {
						width: 88px;
						float: left;
					}
					&:after {
						display: block;
						content: '';
						clear: both;
					}
				}
				nav.primary-menu {
					float: right;
					ul {
						list-style: none;
						li {
							float: left;
							padding: 0 22px;
							line-height: 62px;
							position: relative;
							&:after {
								content: '';
								position: absolute;
							}
							&:hover &:after {
								position: absolute;
								width: 80px;
								height: 2px;
								background-color: #ff5b64;
								content: '';
								left: 50%;
								transform: translateX(-50%);
								bottom: 0;
							}
							a {
								text-decoration: none;
								color: #e1e1e1;
								font-size: 20px;
								position: relative;
							}
							/*active代表点击的状态*/
							.active {
								color: #e7575e;
								span {
									position: absolute;
									width: 80px;
									height: 2px;
									background-color: #ff5b64;
									content: '';
									top: 36px;
									left: 0;
									animation:easyLi 0.6s forwards;
								}
							}
						}
						&:after {
							display: block;
							content: '';
							clear: both;
						}
					}
				}
				.toggle{
					position:absolute;
					width: 30px;
					height: 60px;
					padding:0 5px;
					right: 0;
					top: 0;
					span{
						position: absolute;
						height:2px;
						width:0;
						left: 0;
						top: 50%;
						transform: translateY(-50%);
						transition: all .3s;
					}
					.toggle_false{
						&:before{
							content:'';
							position: absolute;
							width:20px;
							height:2px;
							background-color:#fff;
							top: 6px;
						}
						&:after{
							content:'';
							position: absolute;
							width:20px;
							height:2px;
							background-color:#fff;
							top: -6px;
						}
					}
					.toggle_true{
						&:before{
							content:'';
							position: absolute;
							width:20px;
							height:2px;
							background-color:#fff;
							transform: rotate(45deg);
						}
						&:after{
							content:'';
							position: absolute;
							width:20px;
							height:2px;
							background-color:#fff;
							transform: rotate(-45deg);
						}
					}
				}
				
				&:after {
					display: block;
					content: '';
					clear: both;
				}
			}
		}
	}
	
	@media only screen and (max-width:768px) {
		header {
			.toggle_list{
				li{
					.active{
						background:#fff;
						color: #373738;
					}
				}
			}
			.container {
				.header-inner {
					.el-icon-menu {
						display: none;
					}
				}
			}
		}
	}
	
	@media only screen and (max-width:680px) {
		header {
			.container {
				height: 60px;
				padding: 7.5px 15px;
				.header-inner {
					line-height: 45px;
					text-align: right;
					.site-logo {
						img {
							width: 60px;
						}
					}
					.primary-menu {
						display: none;
					}
					.el-icon-menu {
						display: inline-block;
					}
				}
			}
		}
	}
	
	@media only screen and (min-width:768px) {
		header {
			.container {
				width: 750px;
				.header-inner {
					.toggle{
						display: none;
					}
				}
			}
		}
	}
	
	@media only screen and (min-width:992px) {
		header {
			.container {
				width: 970px;
				.header-inner {
					.toggle{
						display: none;
					}
				}
			}
		}
	}
	
	@media only screen and (min-width:1200px) {
		header {
			.container {
				width: 1170px;
				.header-inner {
					.toggle{
						display: none;
					}
				}
			}
		}
	}
	
	@keyframes easyLi{
		from{
			width: 0;
			left: 0;
			}
		to{
			width:100%;
		}
	}
	.header-item{
			a{
				text-decoration: none;
				color: #e1e1e1;
				font-size: 20px;
				position: relative;
				&:hover{
					color:#e7575e!important;
					span{
						position: absolute;
						width: 80px;
						height: 2px;
						background-color: #ff5b64;
						content: '';
						top: 36px;
						left: 0;
						animation:easyLi 0.6s forwards;
					}
				}
			}
	}

	
</style>


